import { Component, Directive, Output, EventEmitter, Input, ChangeDetectionStrategy } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-unassign-data-group-modal',
  templateUrl: './unassign-data-group-modal.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class UnassignDataGroupModalComponent {
  /**
   * Component context
   */
  readonly hostRef: UnassignDataGroupModalComponent = this;

  /**
   * Flag to show modal
   */
  @Input() isOpen = false;

  @Input() activeDataGroup = 'account';
  @Input() jobRole!: string | undefined;
  @Input() accountGroupName!: string;
  @Input() payeeGroupName!: string;

  /**
   * Event emitter that emits on cancel button click
   */
  @Output() cancel = new EventEmitter<void>();

  /**
   * Event emitter that emits on confirm button click
   */
  @Output() confirm = new EventEmitter<void>();

  /**
   * Function to call on cancel button click
   */
  onCancel(): void {
    this.cancel.emit();
  }

  /**
   * Function to call on confirm button click
   */
  onConfirm(): void {
    this.confirm.emit();
  }
}

@Directive({
  selector: '[bbUnassignDataGroupModalCustomizable]',
})
export class UnassignDataGroupModalCustomizableDirective extends BbTemplate<UnassignDataGroupModalComponent, void> {}
